.posts {
    text-align: left;
    margin: 10px;
    padding: 10px;
}

.posts-header {
    min-width: 400px;
    min-height: 30px;
}

.post {
    border: 1px solid purple;
    margin: 10px;
    padding: 10px;  
}

.btn-add-post {
    float: right;
    position: relative;
    bottom: 42px;
    right: 41px;
}

.posts-list {
    min-width: 400px;
    min-height: 100px;
    max-height: 380px;
    overflow: scroll;
    margin: 5px;
    padding: 5px;
    border: solid black 2px;
}